@import url(https://fonts.googleapis.com/css?family=Sniglet);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

nav {
  width: 100%;
  height: 8vh;
  background: #00132b;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: "Sniglet", cursive;
}

ul {
  list-style: none;
}

ul a {
  text-decoration: none;
  font-size: 1.4rem;
  color: #c5e2ed;
}

ul a span {
  color: #f95900;
  font-weight: bold;
}

/* Styling the Image Card */
.images {
  margin-top: 0.5rem;
  margin-bottom: 2rem;
  padding: 0 0.5rem;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 0.5rem;
}

.images--card {
  border: 2px solid #bed0db;
  border-radius: 3px;
  padding-bottom: 1rem;
  transition: all 0.5s;
}

.images--card:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  border-color: #7f9db0;
}

.images--card img {
  width: 100%;
  height: 250px;
}

.images--card span {
  padding: 0 0.2rem;
}

/* Media Queries  */
@media (min-width: 600px) {
  .images {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 900px) {
  .images {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1200px) {
  .images {
    width: 1300px;
    margin-left: auto;
    margin-right: auto;
    grid-template-columns: repeat(4, 1fr);
  }
}

.search {
  text-align: center;
  width: 100%;
}

.search input {
  margin-top: 0.5rem;
  padding-left: 0.2rem;
  width: 14rem;
  height: 2.3rem;
  border: none;
  border-bottom: 2px solid #f95900;
  color: #7f9db0;
}

.search input:focus {
  outline: none;
}

::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #7f9db0;
  opacity: 1; /* Firefox */
}

::-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #7f9db0;
  opacity: 1; /* Firefox */
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #7f9db0;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #7f9db0;
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: #7f9db0;
}

.search select {
  position: relative;
  margin-top: 0.5rem;
  width: 2.5rem;
  height: 2rem;
  border: none;
  border-bottom: 2px solid #f95900;
  outline: none;
  left: -92px;
  color: #7f9db0;
}

